import getLeague from "./getLeague";
import getRocket from "./getRocket";

export default function getClick(score, rocket, rocketNum, skins) {
  const league = getLeague(score);
  if (league === "bronze") {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        score = 2 * (getRocket(rocketNum) - 1) * 2;
      } else {
        score = 2 * (getRocket(rocketNum) - 1);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        score = 1 * (getRocket(rocketNum) - 1) * 2;
      } else {
        score = 1 * (getRocket(rocketNum) - 1);
      }
    }
  } else if (league === "silver") {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        score = 4 * (getRocket(rocketNum) - 1) * 2;
      } else {
        score = 4 * (getRocket(rocketNum) - 1);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        score = 2 * (getRocket(rocketNum) - 1) * 2;
      } else {
        score = 2 * (getRocket(rocketNum) - 1);
      }
    }
  // } else if (league === "gold") {
  } else {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        score = 8 * (getRocket(rocketNum) - 1) * 2;
      } else {
        score = 8 * (getRocket(rocketNum) - 1);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        score = 4 * (getRocket(rocketNum) - 1) * 2;
      } else {
        score = 4 * (getRocket(rocketNum) - 1);
      }
    }
  // } else if (league === "platinum") {
  //   if (rocket) {
  //     if (skins.includes("pixelcoin")) {
  //       score = 14 * (getRocket(rocketNum) - 1) * 2;
  //     } else {
  //       score = 14 * (getRocket(rocketNum) - 1);
  //     }
  //   } else {
  //     if (skins.includes("pixelcoin")) {
  //       score = 7 * (getRocket(rocketNum) - 1) * 2;
  //     } else {
  //       score = 7 * (getRocket(rocketNum) - 1);
  //     }
  //   }
  // } else {
  //   if (rocket) {
  //     if (skins.includes("pixelcoin")) {
  //       score = 20 * (getRocket(rocketNum) - 1) * 2;
  //     } else {
  //       score = 20 * (getRocket(rocketNum) - 1);
  //     }
  //   } else {
  //     if (skins.includes("pixelcoin")) {
  //       score = 10 * (getRocket(rocketNum) - 1) * 2;
  //     } else {
  //       score = 10 * (getRocket(rocketNum) - 1);
  //     }
  //   }
  }
  return score;
}
