import getLeague from "./getLeague";
import getRocket from "./getRocket";

export default function changeClick(score, rocket, rocketNum, skins) {
  const league = getLeague(score);
  if (league === "bronze") {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        score = score + 2 * (getRocket(rocketNum) - 1) * 2;
      } else {
        score = score + 2 * (getRocket(rocketNum) - 1);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        score = score + 1 * (getRocket(rocketNum) - 1) * 2;
      } else {
        score = score + 1 * (getRocket(rocketNum) - 1);
      }
    }
  } else if (league === "silver") {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        score = score + 4 * (getRocket(rocketNum) - 1) * 2;
      } else {
        score = score + 4 * (getRocket(rocketNum) - 1);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        score = score + 2 * (getRocket(rocketNum) - 1) * 2;
      } else {
        score = score + 2 * (getRocket(rocketNum) - 1);
      }
    }
    // } else if (league === "gold") {
  } else {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        score = score + 8 * (getRocket(rocketNum) - 1) * 2;
      } else {
        score = score + 8 * (getRocket(rocketNum) - 1);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        score = score + 4 * (getRocket(rocketNum) - 1) * 2;
      } else {
        score = score + 4 * (getRocket(rocketNum) - 1);
      }
    }
  }
  return score;
}
