import getLeague from "./getLeague";
import getRocket from "./getRocket";

export default function changeClickSession(
  score,
  session,
  rocket,
  rocketNum,
  skins
) {
  const league = getLeague(score);
  if (league === "bronze") {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        session = session + 2 * (getRocket(rocketNum) - 1) * 2;
      } else {
        session = session + 2 * (getRocket(rocketNum) - 1);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        session = session + 1 * (getRocket(rocketNum) - 1) * 2;
      } else {
        session = session + 1 * (getRocket(rocketNum) - 1);
      }
    }
  } else if (league === "silver") {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        session = session + 4 * (getRocket(rocketNum) - 1) * 2;
      } else {
        session = session + 4 * (getRocket(rocketNum) - 1);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        session = session + 2 * (getRocket(rocketNum) - 1) * 2;
      } else {
        session = session + 2 * (getRocket(rocketNum) - 1);
      }
    }
  } else {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        session = session + 8 * (getRocket(rocketNum) - 1) * 2;
      } else {
        session = session + 8 * (getRocket(rocketNum) - 1);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        session = session + 4 * (getRocket(rocketNum) - 1) * 2;
      } else {
        session = session + 4 * (getRocket(rocketNum) - 1);
      }
    }
  }
  return session;
}
