import getLeague from "./getLeague";
import getRocket from "./getRocket";

export default function getScore(score, rocket, rocketNum, skins) {
  const league = getLeague(score);
  if (league === "bronze") {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        score = 250 * getRocket(rocketNum) * 2;
      } else {
        score = 250 * getRocket(rocketNum);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        score = 250 * 2;
      } else {
        score = 250;
      }
    }
  } else if (league === "silver") {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        score = 400 * getRocket(rocketNum) * 2;
      } else {
        score = 400 * getRocket(rocketNum);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        score = 400 * 2;
      } else {
        score = 400;
      }
    }
    // } else if (league === "gold") {
  } else {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        score = 800 * getRocket(rocketNum) * 2;
      } else {
        score = 800 * getRocket(rocketNum);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        score = 800 * 2;
      } else {
        score = 800;
      }
    }
  }
  // else if (league === "platinum") {
  //   if (rocket) {
  //     if (skins.includes("pixelcoin")) {
  //       score = 1600 * getRocket(rocketNum) * 2;
  //     } else {
  //       score = 1600 * getRocket(rocketNum);
  //     }
  //   } else {
  //     if (skins.includes("pixelcoin")) {
  //       score = 1600 * 2;
  //     } else {
  //       score = 1600;
  //     }
  //   }
  // } else {
  //   if (rocket) {
  //     if (skins.includes("pixelcoin")) {
  //       score = 4000 * getRocket(rocketNum) * 2;
  //     } else {
  //       score = 4000 * getRocket(rocketNum);
  //     }
  //   } else {
  //     if (skins.includes("pixelcoin")) {
  //       score = 4000 * 2;
  //     } else {
  //       score = 4000;
  //     }
  //   }
  // }
  return score;
}
