import getLeague from "./getLeague";
import getRocket from "./getRocket";

export default function changeScoreSession(
  balance,
  session,
  rocket,
  rocketNum,
  skins
) {
  const league = getLeague(balance);
  if (league === "bronze") {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        session = session + 250 * getRocket(rocketNum) * 2;
      } else {
        session = session + 250 * getRocket(rocketNum);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        session = session + 250 * 2;
      } else {
        session = session + 250;
      }
    }
  } else if (league === "silver") {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        session = session + 400 * getRocket(rocketNum) * 2;
      } else {
        session = session + 400 * getRocket(rocketNum);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        session = session + 400 * 2;
      } else {
        session = session + 400;
      }
    }
  } else {
    if (rocket) {
      if (skins.includes("pixelcoin")) {
        session = session + 800 * getRocket(rocketNum) * 2;
      } else {
        session = session + 800 * getRocket(rocketNum);
      }
    } else {
      if (skins.includes("pixelcoin")) {
        session = session + 800 * 2;
      } else {
        session = session + 800;
      }
    }
  }
  return session;
}
