import { useState } from "react";
import getColor from "../../utils/getColor";

export default function JoinSquadPhoto({ urlPhoto, firstName }) {
  const [hasError, setHasError] = useState(false);
  return (
    <>
      {urlPhoto !== "" && !hasError ? (
        <img
          src={`${urlPhoto}`}
          style={{
            width: "112px",
            height: "112px",
            borderRadius: "50%",
          }}
          onError={() => {
            setHasError(true);
          }}
        />
      ) : (
        <div
          style={{
            width: "112px",
            height: "112px",
            borderRadius: "50%",
            background: getColor(firstName.substring(0, 1)),
          }}
        >
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              fontSize: "28px",
              fontWeight: "600",
              color: "#fff",
              marginTop: "40px",
            }}
          >
            {firstName.substring(0, 2).toUpperCase()}
          </div>
        </div>
      )}
    </>
  );
}
