import "./style.scss";
import { useState, useEffect } from "react";
import {
  BrowserRouter as Router,
  Route,
  useNavigate,
  useLocation,
} from "react-router-dom";
import { CSSTransition } from "react-transition-group";
import { BackButton } from "@twa-dev/sdk/react";
import WebApp from "@twa-dev/sdk";
